import { Component, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { TranslateService } from '@ngx-translate/core';
import { ToolsProvider } from '../../providers/tools/tools';
import { ReservationServiceProvider } from '../../providers/reservation-service/reservation-service';
import { DomSanitizer } from '@angular/platform-browser';
import { FormBuilder, FormGroup, Validators, FormControl, FormArray } from '@angular/forms';
import { CommonServiceProvider } from '../../providers/common-service/common-service';
import { NotificationProvider } from '../../providers/notification/notification';
/**
 * Generated class for the CheckoutBasicinfoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-checkout-basicinfo',
  templateUrl: 'checkout-basicinfo.html',
})
export class CheckoutBasicinfoPage {
  welcomeMessage: any;
  frmCheckoutBasicInfo: FormGroup;
  monthNames: any; // for month names
  monthShortNames: any = []; // for month short names
  isCheckedAgree: boolean = false;

  constructor(public navCtrl: NavController, public navParams: NavParams, private tools: ToolsProvider,
    public reservation: ReservationServiceProvider, public santizer: DomSanitizer, public cdRef: ChangeDetectorRef,
    public formBuilder: FormBuilder, public alertCtrl: AlertController, public commonProvider: CommonServiceProvider, private notificationProvider: NotificationProvider
  ) {
    this.frmCheckoutBasicInfo = formBuilder.group({
      'txtFirstName': new FormControl('', [Validators.required]),
      'txtLastName': new FormControl('', [Validators.required]),
      'txtEmail': new FormControl('', [Validators.required]),
    });
    this.frmCheckoutBasicInfo.controls.txtFirstName.setValue(this.reservation.objReservation.guestName);
    this.frmCheckoutBasicInfo.controls.txtLastName.setValue(this.reservation.objReservation.lastName);
    this.frmCheckoutBasicInfo.controls.txtEmail.setValue(this.reservation.objReservation.email);

    // translate month names
    let monthList = this.tools.fnLanguageTranslate("common.monthNames");
    monthList.subscribe((value: string) => {
      this.monthNames = value;
    });
    //translated month short names
    for (let i = 0; i < 12; i++) {
      this.monthShortNames.push(this.tools.getShortMonthName(i));
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CheckoutBasicinfoPage');
    this.fnCheckPreEnteredFormData();


    let welcomeMsgTranslate = this.tools.fnLanguageTranslate("view.checkout.template.welcome");
    let messageHtml = '';
    welcomeMsgTranslate.subscribe((value: string) => {
      messageHtml = value;
    });

    this.welcomeMessage = messageHtml != "" ? messageHtml.replace('{nombreHuesped}', this.reservation.objReservation.guestName) : "";
    this.welcomeMessage = this.santizer.bypassSecurityTrustHtml(this.welcomeMessage);//By Passing the Security Trust
    this.cdRef.detectChanges();
  }

  //Navigates to the previous page
  public fnBack() {
    this.navCtrl.pop({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
  }

  ionViewWillLeave() {
    if (this.commonProvider.objFormData && this.commonProvider.objFormData.checkout.checkoutCompleted != true) {
      this.commonProvider.objFormData.checkout.firstName = this.frmCheckoutBasicInfo.controls['txtFirstName'] && this.frmCheckoutBasicInfo.get('txtFirstName').value;
      this.commonProvider.objFormData.checkout.lastName = this.frmCheckoutBasicInfo.controls['txtLastName'] && this.frmCheckoutBasicInfo.get('txtLastName').value;
      this.commonProvider.objFormData.checkout.email = this.frmCheckoutBasicInfo.controls['txtEmail'] && this.frmCheckoutBasicInfo.get('txtEmail').value;
      this.commonProvider.objFormData.checkout.isCheckedAgree = this.isCheckedAgree;
      this.commonProvider.objFormData.checkout.checkoutCompleted = false;
    }
  }

  // fetch pre-entered data into fields from model
  fnCheckPreEnteredFormData() {
    this.commonProvider.objFormData.checkout.checkoutCompleted = false;
    let firstName = (this.commonProvider.objFormData.checkout.firstName) ? this.commonProvider.objFormData.checkout.firstName : this.frmCheckoutBasicInfo.controls['txtFirstName'].value;
    let lastName = (this.commonProvider.objFormData.checkout.lastName) ? this.commonProvider.objFormData.checkout.lastName : this.frmCheckoutBasicInfo.controls['txtLastName'].value;
    let email = (this.commonProvider.objFormData.checkout.email) ? this.commonProvider.objFormData.checkout.email : this.frmCheckoutBasicInfo.controls['txtEmail'].value;
    let isChecked = (this.commonProvider.objFormData.checkout.isCheckedAgree) ? this.commonProvider.objFormData.checkout.isCheckedAgree : this.isCheckedAgree;


    this.frmCheckoutBasicInfo.controls['txtFirstName'] && this.frmCheckoutBasicInfo.get("txtFirstName").setValue(firstName);
    this.frmCheckoutBasicInfo.controls['txtLastName'] && this.frmCheckoutBasicInfo.get("txtLastName").setValue(lastName);
    this.frmCheckoutBasicInfo.controls['txtEmail'] && this.frmCheckoutBasicInfo.get("txtEmail").setValue(email);
    this.isCheckedAgree = isChecked;
  }

  //Triggers while clicking the continue button
  fnOnContinueClick() {
    let emptyFields = [];  // to add empty fields in the form
    let alertMessage: any;  // to show alert message on validation

    let validateMessages = this.tools.fnLanguageTranslate(
      ['view.checkin.label.nombre.title',
        'view.checkin.label.apellidos.title'
      ]);
    if (this.frmCheckoutBasicInfo.controls.txtFirstName.hasError('required')) {  // checks Date Of Issue field is empty 
      validateMessages.subscribe((value: string) => {
        emptyFields.push(value['view.checkin.label.nombre.title']);
      });
    }
    if (this.frmCheckoutBasicInfo.controls.txtLastName.hasError('required')) { // checks Date Of Issue field is empty 
      validateMessages.subscribe((value: string) => {
        emptyFields.push(value['view.checkin.label.apellidos.title']);
      });
    }

    let msg = this.tools.fnLanguageTranslateWithParam(["common.mandatoryFields", "view.login.error.emptyMail",
      "common.error", "buttons.ok"], { fields: emptyFields });

    if (this.frmCheckoutBasicInfo.controls.txtFirstName.hasError('required') ||
      this.frmCheckoutBasicInfo.controls.txtLastName.hasError('required')) {
      msg.subscribe((value: string) => {
        alertMessage = value['common.mandatoryFields'];
      });

      msg.subscribe((value: string) => {
        let alertnew = this.alertCtrl.create({
          title: value['common.error'],
          message: alertMessage,
          cssClass: 'cls_alrt',
          buttons: [
            {
              text: value['buttons.ok'],
              role: 'cancel'
            }]
        });
        alertnew.present();
        setTimeout(() => { alertnew.dismiss(); }, 5000);
      });
    }//checking if agreed to terms at the success of card no & card holder validations
    else if (this.isCheckedAgree == false) {
      let msg = this.tools.fnLanguageTranslate(["common.error", "view.checkin.error.mustAgree", "buttons.ok"]); // translate of error message
      msg.subscribe((value: string) => {
        let alertnew = this.alertCtrl.create({
          title: value['common.error'],
          message: value["view.checkin.error.mustAgree"],
          cssClass: 'cls_alrt',
          buttons: [
            {
              text: value['buttons.ok'],
              role: 'cancel'
            }]
        });
        alertnew.present();
        setTimeout(() => { alertnew.dismiss(); }, 5000);
      });
    }
    else {
      this.navCtrl.push("CheckoutPaymentPage", {}, { animate: true, animation: 'transition', duration: 500, direction: 'forward' });
    }
  }

  //function to handle checking of radio button
  public fnOnRadioSelectAgree($event) {
    this.isCheckedAgree = !this.isCheckedAgree;
  }
}



